*{
  margin: 0;
  padding: 0;
}
.app-input{
  text-align: center;
  color:blueviolet;
}
h1{
  color:darkblue;
}
.app-input input{
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid blue;
  outline:none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button{
  background-color: rgb(147, 192, 35);
  color: rgb(16, 15, 15);
  border:none;
  outline: none;
  padding: 10px 10px;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
}
nav{
  background-color: burlywood;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgb(162, 8, 8);
  font-weight: bold;
}
nav ul li{
  cursor: pointer;
  font-size: 18px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.4);
}
.navActive{
  color:darkmagenta;
  font-size: 30px;
  font-weight: bold;
}
.app-container{
  background-color: aqua;
  font-size: 30px;
}

